.footer {
    font-size: 18px;
    text-align: center;
    margin-top: 0;
    margin-bottom: 20px;
    position: absolute;
    bottom: 12px;
    font-weight: bold;
  }
  .footer a {
    margin-left: 4px;
    text-decoration: underline;
    color: #2600ff;
  }
  
  
  @media (max-width: 930px) {
    .footer {
      color: orange;
      margin-top: 100px;
      bottom: 10px;
      position: relative;
      font-size: 17px;
    }
    .footer a {
      color: rgb(141, 218, 25);
    }
    .footer-noToDos {
      position: absolute;
      bottom: 12px;
    }
  }