.EmptyToDos{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    margin: auto;
    margin-top: 20px;
    background-color: #AEE2FF;
    border-radius: 8px;
    box-shadow: 7px 14px 0px #000000;
    border: 2px solid ; 
    width: 70%;
  }
  .EmptyToDos-button{
    background-color: #FFC107 ;
    box-shadow: 4px 8px 0px #000000;
    border: 2px solid #000000;
    border-radius: 8px;
    cursor: pointer;
    font-weight: bolder;
    color: #000000;
    height: 80px;
    width: 50%;
    margin-bottom: 36px;
    transition: 0.6s;
    font-size: 18px;
  }
  .EmptyToDos-button:hover {
    transform: translateY(-6px);
    transition: 0.6s;
  }
  .arrow-down {
    text-align: center;
    padding: 0 0 12px 0;
    animation-name: arrow;
    animation-duration: 1s;
    font-size: 50px;
    animation-iteration-count: infinite;
    animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }
  
  @keyframes arrow {
    0% {
      transform: translateY(12px);
    }
    50% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(12px);
    }
  }
  
  
  @media (max-width: 930px) {
    .EmptyToDos-button{
      width: 55%;
    }
  }